.header__wrapper {
  display: flex;
  position: relative;
  min-height: 100px;
}

.header__logo {
  position: absolute;
  right: 15px;
  top: 15px;
  img {
    width: 112px;
    height: 34px;
  }
}

.header__phone {
  font-weight: 600;
  font-size: 14px;
  line-height: 14px;
  color: #ffffff;

  position: absolute;
  right: 15px;
  top: 53px;
  height: max-content;

  &:hover,
  :focus {
    color: #1c3374;
  }

  &:active {
   color: #0266c1;
  }
}

.header__burger-close {
  display: block;
  position: absolute;
  top: 15px;
  width: 26px;
  height: 18.91px;
  background: url('/img/burger.png') no-repeat;
}

.header__burger-open {
  position: absolute;
  top: 15px;
  left: 15px;
  width: 20px;
  height: 20px;
  background: url('/img/burger-close.png') no-repeat;
}

.header__list {
  display: none;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style: none;
  font-weight: 700;
  font-family: 14px;
  letter-spacing: 0.04rem;
  padding-top: 99px;
}

.header__item {
  margin-bottom: 22px;
  text-transform: uppercase;
  padding: 4px;
  height: max-content;

  &:last-child {
    margin-bottom: 0;
  }

  &:hover {
    box-shadow: inset 0 -1px 0 0 #ffffff;
  }

  &:active {
  box-shadow: #ffffff 0px 0px 0px 1px inset;
  }
}

.header__box {
  width: 100%;
  background-color: #ffffff00;
}


@media (min-width: $tablet) {

  .header__burger {
    position: static;
    margin-top: 42px;
    z-index: 2;
  }

  .header__box {
    display: flex;
    justify-content: space-between;
  }

  .header__logo {
    padding-top: 42px;
    position: static;

    img {
      width: 144px;
      height: 44px;
    }
  }

  .header__list {
    position: absolute;
  }

  .header__phone {
    padding-top: 42px;
    position: static;
    font-size: 16px;
  }
}

@media (min-width: $desktop) {
  .header__burger-close {
    display: none;
  }

  .header__list {
    position: static;
    display: flex;
    flex-direction: row;
    font-weight: 400;
    font-size: 16px;
    color: #ffffff;
    margin-top: 60px;
    padding-top: 0;
  }

  .header__item {
    margin-right: 17px;
    text-transform: none;

    &:last-child {
      margin-right: 0;
    }
  }

  .header__item-link {
    color: #ffffff;
  }

  .header__logo {
     img {
      width: 205px;
      height: 62px;
    }
  }

  .header__phone {
    margin-top: 18px;
    font-size: 18px;
  }
}
